// Vuex Store
import { createStore } from "vuex";

export default createStore({
  state() {
    return {
      prodList: [
        { id: '1', name: "product", count: 6, price: 100 },
        { id: '2', name: "product1", count: 6, price: 100 },
        { id: '3', name: "product2", count: 6, price: 100 },
        { id: '4', name: "product3", count: 6, price: 100 },
        { id: '5', name: "product4", count: 6, price: 100 }
      ]
    };
  },
  getters: {
    getRecived: (state) => (input) => {
      return state.prodList.find(prodInf => prodInf.name.toLowerCase() === input?.toLowerCase());
    },
    getAllProducts: (state) => (searchString) => {
      if (searchString == null || searchString === '') {
        return state.prodList; 
      }
      return state.prodList.filter(uni => uni.name.toLowerCase().includes(searchString.toLowerCase()));  
    },
  }
});
