<template>
    <div>
      <input type="text" v-model="reciveString" placeholder="Enter name to create send check">
      <div v-if="result">
        <p>Product Name: {{ result.name }}</p>
        <p>Quantity: {{ result.count }}</p>
        <p>Profit: {{ result.count * result.price-(result.count * result.price)*0.3 }}</p>
        <p>Delivery: {{  (result.count * result.price)*0.3}}</p>
      </div>
      <button @click="getCheck" >Get Send Check</button>
      <div v-for="prod in resList" :key="prod.name">
        {{ prod }}
      </div>
    </div>
  </template>
  
    
  <script>
  import { mapGetters } from 'vuex';
  export default {
    name: "GetSend",
    data() {
      return {
        reciveString: " ",
        result: {},
        resList:[]
      };
    },
    computed: {
      ...mapGetters(['getRecived'])
    },
    methods:{
      getCheck(){
        return this.resList.push(this.result)
      }
    },
    watch: {
      reciveString(newVal) {
        this.result = this.getRecived(newVal);
      }
  
    }
  };
  </script>
    <style lang="scss" scoped>
    </style>