<template>
  <div class="search-container">
    <input type="text" v-model="searchString" placeholder="Search products..." />
    <div class="product-list">
      <div v-for="prod in getproductByrequest" :key="prod.id" class="product-item">
        <p><strong>Name:</strong> {{ prod.name }}</p>
        <p><strong>Quantity:</strong> {{ prod.count }}</p>
        <p><strong>Price:</strong> {{ prod.price }}$</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  name: "GetProduct",
  data() {
    return {
      searchString: null
    };
  },
  computed: {
    ...mapGetters(['getAllProducts']),
    getproductByrequest() {
      return this.getAllProducts(this.searchString);
    }
  },
};
</script>

