<template>
  <div class="recive-container">
    <input type="text" v-model="reciveString" placeholder="Enter product name" />
    <button @click="getCheck">Add to Check</button>
    <div v-if="result" class="check-result">
      <p><strong>Name:</strong> {{ result.name }}</p>
      <p><strong>Quantity:</strong> {{ result.count }}</p>
      <p><strong>Total:</strong> {{ result.count * result.price }}$</p>
    </div>
    <div v-for="prod in resList" :key="prod.name" class="check-item">
      {{ prod.name }} - {{ prod.count }} pcs
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  name: "GetRecive",
  data() {
    return {
      reciveString: null,
      result: null,
      resList: []
    };
  },
  computed: {
    ...mapGetters(['getRecived'])
  },
  methods: {
    getCheck() {
      if (this.result) {
        this.resList.push(this.result);
      }
    }
  },
  watch: {
    reciveString(newVal) {
      this.result = this.getRecived(newVal);
    }
  }
};
</script>

<style scoped lang="scss">
.recive-container {
  max-width: 400px;
  margin: auto;
}
input, button {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
}
button {
  background-color: #42b983;
  color: #fff;
  border: none;
  cursor: pointer;
}
.check-result, .check-item {
  background: #e6f7e9;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
}
</style>
